<template>
  <div class="test-score-container">
  <div class="page-form-checkbox">
    <ul class="listview listview-form">
      <li class="height-auto no-bor">
        <v-radio v-model="value1" :options="options1" @change="handleChange"></v-radio>
      </li>
    </ul>
  </div>
  </div>
</template>

<script>
  // import logger from '../js/utils/logger';
  import VRadio from '../vendor/v-radio';
  import VRadioTag from '../vendor/v-radio-tag';
  import api from '../utils/api';
  import { mapGetters, mapMutations } from 'vuex';

  export default {
    name: 'test-score',
    components: {
      VRadio,
      'v-v-radio-tag': VRadioTag
    },
    data () {
      return {
        value1: '',
        options1: [],
        id: '' // 考试id
      };
    },
    created () {
      this.getScoreRange();
    },
    computed: {
      ...mapGetters(['getOriginType'])
    },
    activated () {
      // 获取当前发布页是从哪个页面过来的
      this.$route.query.origin = this.getOriginType;
      if (this.$route.query.origin === 'listPage') {
        this.value1 = '';
      }
    },
    mounted () {
    },
    methods: {
      ...mapMutations(['SET_SCORE', 'SET_SCORE_VALUE', 'SET_EXAM_ID', 'SET_ORIGIN_TYPE']),
      getScoreRange () {
        let vm = this;
        let param = {};
        api.getFindExamList(param).then(function (res) {
          if (res.data.code - 0 === 0) {
            vm.options1 = res.data.data;
            vm.options1.forEach((item, index) => {
              vm.value1 = item.exName;
              vm.id = item.exId;
              // console.log(vm.value1, '99995555');
              // console.log(vm.id, '99995555');
            });
            // vm.SET_SCORE_VALUE(vm.value1);
            // vm.SET_EXAM_ID(vm.id);
            // console.log(vm.value1, 'test');
            // console.log(vm.id, 'test');
            // vm.$router.go(-1);
          }
        });
      },
      handleChange (e) {
        this.SET_SCORE_VALUE(e.exName);
        this.SET_EXAM_ID(e.exId);
        this.SET_ORIGIN_TYPE('testPage');
        this.$router.go(-1);
      }
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss">
  @import "../scss/variables";
  @import "../scss/mixins";
  .test-score-container{
    width: 100%;
    height: 100%;
    background: #f2f2f4;
  }
  .page-form-checkbox{
    background: #f2f2f4;
    .blank {
      width: 100%;
      height: pxTorem(100px);
    }
    .listview-form{
      .no-bor{
        border:none;
      }
      .v-radio__value{
        padding:0;
      }
      .v-checkbox__icon{
        background-size:100%;
      }
      .v-radio.list label{
        height:44px;
        line-height: 44px;
        border-bottom:1px solid #e1e1e1!important;/*no*/
        font-size: 15px;
        color: #383838;
      }
      .v-radio.list label .v-radio__input:checked ~ .v-radio__icon{
        color:#C3303A!important;
      }
      .v-radio.list label:last-child {
        border: none !important;
      }
    }
  }
</style>



